.row{
    align-items: center;
    padding-top: 120px;
    padding-bottom: 50px;
}

.text .image{
    padding-top: 50px;
    padding-bottom: 50px;
}

.about{
    background: #000;
}

.about .text{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    margin: auto;
}

.about .text h6{
    color: #00BF7F;
    font-weight: 800;
    font-size: 2rem;
    letter-spacing: 2px;
}

.about .text h2{
    color: #F7F5F4;
    font-weight: 700;
    font-size: 3.5rem;
    letter-spacing: 2px;
}

.about .text p{
    color: #F7F5F4;
    font-weight: 400;
    font-size: 1.3rem;
    letter-spacing: 0.5px;
}

h5{
    color: #F7F5F4;
}

.react-tabs__tab{
  color: #F7F5F4;
}

.react-tabs__tab--selected{
  color: #F7F5F4;
  font-weight: 600;
  background: #00BF7F;
}

.react-tabs__tab-panel{
  color: #F7F5F4;
}


