.project-image {
  display: block;
  width: 250px;
  height: 260px;
}

.overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgb(0, 0, 0); /* Fallback color */
  background: rgba(0, 0, 0, 0.5); /* Black background with 0.5 opacity */
  color: #f1f1f1; /* Grey text */
  overflow: hidden;
  width: 0;
  height: 260px;
  transition: .5s ease;
}

.image-hover:hover .overlay {
  width: 262px;
  left: 0;
}

.project-text {
  color: white;
  font-size: 25px;
  position: absolute;
  top: 25%;
  left: 51%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  white-space: nowrap;
}

.project-text2 {
  color: white;
  font-size: 18px;
  position: absolute;
  top: 45%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  white-space: nowrap;
}