.mid{
    width: 100%;
    height: 100vh;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    text-align: center;
}

.mid video{
    width: 100%;
    height: 100%;
    pointer-events: none;
    object-fit: cover;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}

.hero{
    position: relative;
    text-align: center;
}

.heading-text{
    font-size: 40px;
    color: white;
    font-weight: bold;
}

.mid .hero p{
    color: whitesmoke;
    width: 100%;
    letter-spacing: 0.4rem;
    text-align: center;
    padding-bottom: 2.5rem;
}

.mid .hero a{
    color: rgb(63, 64, 65);
    background: #00BF85;
    font-size: 15px;
    font-weight: 600;
    padding: 1.5rem 1.8rem;
    border-radius: 30px;
    text-decoration: none;
}

.mid .hero a:hover{
    background: #fff;
}